@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;1,100;1,200;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* mobile */

.app-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
    -webkit-align-items: stretch;
            align-items: stretch;
}

.app-container-nav {
    width: 100%;
    padding-right: 28px;
}

.app-container-content {
    width: 100% - 180px;
}

/* desktop */

@media screen and (min-width: 600px) {
    .app-container {
        -webkit-flex-direction: row;
                flex-direction: row;
    }
    .app-container-nav {
        width: 228px;
        -webkit-flex: 0 0 auto;
                flex: 0 0 auto;
    }
    .app-container-content {
        width: 100%;
    }
}

/* mobile */
.home-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: auto;
  width: calc(100% - 20px);
  margin-top: 60px;
  padding: 10px;
  padding-bottom: 20px;
}

.home-item {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: calc(100vh - 60px);
  max-width: 100%;
  overflow: hidden;
  background: url(/static/media/purplebox.f93488a4.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.home-item img {
  height: 100%;
  width: auto;
  object-fit: cover;
}

.home-caption {
  background-color: rgba(255, 255, 255, 0.35);
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  line-height: 146%;
  font-size: 13px;
  text-align: center;
  position: fixed;
  bottom: 24px;
  max-width: 80%;
  left: 0;
  right: 0;
  margin: 0 auto;
  padding: 4px 2px;

}

.home-caption span {
  font-style: italic;
}

/* desktop */
@media screen and (min-width: 600px) {

  .home-container {
    display: grid;
    grid-template-columns: repeat(12, minmax(0, 1fr));
    grid-column-gap: 32px;
    -webkit-column-gap: 32px;
            column-gap: 32px;
    box-sizing: border-box;

    width: calc(100% - 10px);
    margin-top: 0;
    padding: 20px 10px;
  }

  .home-item {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    grid-column-start: 1;
    grid-column-end: 13;
    max-width: 100%;
    height: calc(100vh - 84px);
    padding: 20px 0;
  }

  .home-item img {
    object-fit: cover;
    max-width: 100%;
    max-height: 100%;
    margin-top: 24px;
  }

  .home-caption {
    font-family: "Montserrat", sans-serif;
    font-weight: 500;
    line-height: 146%;
    font-size: 13px;
    text-align: center;
    position: relative;
    bottom: 0px;
    grid-column-start: 1;
    grid-column-end: 13;
    margin: 0;
    padding-top: 2px;
  }

  .home-caption span {
    font-style: italic;
  }

}

/* mobile */

.work-container {
  margin-top: 48px;
}

div.work-image-row {
display: block;
width: 100% - 20px;
margin-left: 10px;
margin-right: 10px;
}

div.work-image-row img {
max-height: 100%;
max-width: 100%;
object-fit: cover;
margin-top: 24px;
margin-bottom: 24px;
}

div.work-artwork-details {
position: relative;
}

div.work-artwork-details p {
position: absolute;
bottom: -20px;
line-height: 130%;
font-family: "Montserrat", sans-serif;
text-align: left;
font-weight: 400;
font-size: 13px;
}

.work-image-row span {
font-style: italic;
}


/* desktop */
@media screen and (min-width: 600px) {

  .work-container {
    margin-top: 0px;
    padding-left: 10px;
  }

  div.work-image-row {
    display: grid;
    grid-template-columns: repeat(12, minmax(0, 1fr));
    grid-column-gap: 32px;
    -webkit-column-gap: 32px;
            column-gap: 32px;
    box-sizing: border-box;
    width: 100%;
    margin: 0;
  }
  
  div.work-image-row img {
    grid-column-start: 1;
    grid-column-end: 8;
    max-height: 100%;
    object-fit: cover;
    margin-top: 32px;
    margin-bottom: 32px;
  }
  
  div.work-artwork-details {
    position: relative;
    grid-column-start: 8;
    grid-column-end: 11;
  }
  
  div.work-artwork-details p {
    position: absolute;
    bottom: 18px;
    font-family: "Montserrat", sans-serif;
    text-align: left;
    font-weight: 200;
    font-size: 14px;
  }
  
  .work-image-row span {
    font-style: italic;
  }
}
